import MasterPage from '../components/Master';
import fecth from 'isomorphic-unfetch';
import Banner from '../components/Banner';
import News from '../components/News';

const Home = (props) => (


    <MasterPage>
        <div className='row'>
            <div className='col-md-12'>
                <Banner />
                {console.log(props)}

            </div>
            <div className='col-md-8'>
                <h2>Last news</h2>
                
            </div>
            <div className='col-md-4'>
                <h2>Last events</h2>
            </div>
        </div>
    </MasterPage>
)

Home.getInitialProps = async () => {
    const res = await fecth(`https://newsapi.org/v2/everything?q=bitcoin&from=2019-09-10&sortBy=publishedAt&apiKey=46ac7d58caab4edfb208d99b33d30fe6&lenguage=en`);
    const news = await res.json();

    return news;

}

export default Home